.Slider_block{
    background-position: center center;
    background-image: url('/src/img/Slide1.jpg');
    width: 100%;
    height: 700px;
}

.Btn_shop{
    border: 1px solid black;
    width: 150px;
    height: 50px;
    background-color: black;
    color: white;
    display: flex;
    align-items:center;
    justify-content: center;
    margin: auto;
    margin-top: 550px;
    
}

.Btn_shop:hover{
    cursor: pointer;
    color: gray;
    transition-duration: 0.2s;
}

.Arr_block_title{
    text-align: center;
    margin-top: 50px;
}

.Section_Blocks_Sale{
    display: flex;
    align-items: center;
    justify-content: center;
}

.Block_Sale{
    width: 280px;
    height: 360px;
    margin-left: 25px;
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 15px;
}

.Block_Img{
    display: inline-block;
    overflow: hidden;
}

.Block_Sale_Img{
    width: 280px;
    height: 230px;
    margin-bottom: 15px;
    display: block;
    transition: 1s;
}

.Block_Sale_Img:hover{
    transform: scale(2);
}

.Price_Sale{
    margin-top: 20px;
}

.linksHomePage{
    color: white;
    text-decoration: none;
}

.Btn_shop_View{
    border: 1px solid black;
    width: 150px;
    height: 50px;
    background-color: black;
    color: white;
    display: flex;
    align-items:center;
    justify-content: center;
    margin: auto;
    margin-top: 17px;
    margin-bottom: 35px;
    
}

.Btn_shop_View:hover{
    cursor: pointer;
    color: gray;
    transition-duration: 0.2s;
}

.Slider_Block_Second{
    width: 100%;
    height: 400px;
    background-position: 100px center; 
    background-image: url("/src/img/Slide3.webp");
    margin-bottom: 15px;
}


.Categories_Blocks{
    display: flex;
    align-items: center;
    justify-content: center;
}

.Ctegoies_block{
    margin-left: 15px;
    display: block;
    position: relative;
}

.Categoria{
    position: absolute;
    top: 150px;
    left: 70px;
    color: black;
    background-color: white;
    width: 150px;
    height: 45px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.Categoria:hover{
    cursor: pointer;
    color: gray;
    transition-duration: 0.2s;
}

.nameLink{
    text-decoration: none;
    color: black;
    cursor: pointer;
}

@media screen and (max-width: 1250px){
    .Section_Blocks_Sale{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .Block_Sale{
        margin: auto;
    }

    .Categories_Blocks{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .Ctegoies_block{
        margin: auto;
    }

    .Categoria_img{
        width: 280px;
        height: 230px;
    }

    .Btn_shop{
        margin-top: 500px;
    }
}


@media screen and (max-width: 600px){
    .Section_Blocks_Sale{
        display: grid;
        grid-template-columns: 1fr;
    }

    .Block_Sale{
        margin: auto;
    }

    .Categories_Blocks{
        display: grid;
        grid-template-columns:  1fr;
    }

    .Ctegoies_block{
        margin: auto;
    }

    .Categoria_img{
        width: 280px;
        height: 230px;
    }

    .Btn_shop{
        margin-top: 500px;
    }
}

